@font-face {
  font-family: 'Gilroy';
  src: url("../fonts/gilroy-black-webfont.woff2") format("woff2"), url("../fonts/gilroy-black-webfont.woff") format("woff");
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  src: url("../fonts/gilroy-bold-webfont.woff2") format("woff2"), url("../fonts/gilroy-bold-webfont.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  src: url("../fonts/gilroy-medium-webfont.woff2") format("woff2"), url("../fonts/gilroy-medium-webfont.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  src: url("../fonts/gilroy-regular-webfont.woff2") format("woff2"), url("../fonts/gilroy-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

* {
  box-sizing: border-box;
  margin: 0;
}

.body {
  font-family: 'Gilroy', 'Times New Roman', sans-serif;
  font-weight: 700;
  font-style: normal;
  background-image: url(../img/bgc-img.jpg);
  background-position: center top;
  background-repeat: no-repeat;
  background-color: #0B1D26;
}

.container {
  max-width: 1790px;
  min-width: 991px;
  margin: 0 auto;
  padding: 0 15px;
}

.container--second {
  max-width: 1230px;
  min-width: 991px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  margin: 64px -15px 182px;
}

.header-main {
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
}

.header-main__navv a {
  text-decoration: none;
  font-size: 18px;
  line-height: 22px;
  color: #FFFFFF;
  margin: 0 20px;
}

.header-main__login {
  display: flex;
  align-items: center;
  text-decoration: none;
  font-size: 17px;
  line-height: 21px;
  color: #FFFFFF;
}

.header-main__login svg {
  margin-right: 8px;
}

.top-title {
  max-width: 950px;
  position: relative;
  margin-bottom: 860px;
}

.top-title__p {
  font-weight: 900;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 6px;
  text-transform: uppercase;
  color: #FBD784;
  margin-left: 104px;
  margin-bottom: 32px;
}

.top-title__p::before {
  content: '';
  width: 72px;
  height: 2px;
  background-color: #FBD784;
  position: absolute;
  margin-left: -104px;
  margin-top: 9px;
}

.top-title__h {
  font-family: 'Times New Roman';
  font-size: 88px;
  line-height: 100px;
  text-transform: capitalize;
  color: #FFFFFF;
  margin-bottom: 33px;
}

.top-title__link {
  display: flex;
  font-size: 18px;
  line-height: 22px;
  color: #FFFFFF;
  text-decoration: none;
}

.top-title__link svg {
  margin-left: 16px;
  align-items: center;
}

.hiker {
  margin: 0 -15px;
}

.hiker-item {
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 198px;
}

.hiker-item__left {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.hiker-item__left-h {
  font-weight: 900;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 6px;
  text-transform: uppercase;
  color: #FBD784;
  margin-left: 104px;
  margin-bottom: 27px;
}

.hiker-item__left-h::before {
  content: '';
  width: 72px;
  height: 2px;
  background-color: #FBD784;
  position: absolute;
  margin-left: -104px;
  margin-top: 9px;
}

.hiker-item__left-banner {
  font-family: 'Times New Roman';
  font-size: 64px;
  line-height: 74px;
  color: #FFFFFF;
  margin-bottom: 27px;
}

.hiker-item__left-p {
  font-size: 18px;
  line-height: 32px;
  color: #FFFFFF;
  margin-bottom: 27px;
}

.hiker-item__left-link {
  font-size: 18px;
  line-height: 22px;
  color: #FBD784;
  text-decoration: none;
  display: flex;
  align-items: center;
}

.hiker-item__left-link svg {
  margin-left: 16px;
}

.hiker-item__right {
  width: 41.666%;
}

.hiker-iten__right-img {
  display: block;
  width: 100%;
  height: 720px;
  object-fit: cover;
}

.hiker-item__position {
  font-size: 240px;
  line-height: 240px;
  color: #FFFFFF;
  opacity: 0.1;
  position: absolute;
  margin-left: -50px;
  margin-top: -410px;
}

.hiker-item:nth-child(even) {
  flex-direction: row-reverse;
}

.footer {
  margin: 0 -15px;
}

.footer-menu {
  display: flex;
  justify-content: space-between;
}

.footer-menu__item-link {
  text-decoration: none;
}

.footer-menu__item-link img {
  margin-bottom: 28px;
  margin-top: 6px;
}

.footer-menu__item-text {
  max-width: 290px;
  font-size: 18px;
  line-height: 32px;
  color: #FFFFFF;
  margin-bottom: 190px;
}

.footer-menu__item-privacy {
  font-weight: 500;
  font-size: 18px;
  line-height: 32px;
  color: #FFFFFF;
  opacity: 0.5;
  margin-top: auto;
  margin-bottom: 52px;
}

.footer-metu__item-links {
  display: flex;
  flex-direction: column;
  margin-right: 27px;
}

.footer-metu__item-links h2 {
  font-size: 24px;
  line-height: 32px;
  color: #FBD784;
  padding-bottom: 8px;
}

.footer-metu__item-links a {
  font-weight: 500;
  font-size: 18px;
  line-height: 32px;
  color: #FFFFFF;
  text-decoration: none;
  margin-top: 16px;
}

.right-site-bar {
  display: flex;
  flex-direction: column;
  position: fixed;
  right: 80px;
  top: 324px;
  font-size: 18px;
  line-height: 22px;
}

.right-site-bar a {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: #FFFFFF;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 30px;
  border-right: 3px solid rgba(255, 255, 255, 0.5);
}

.right-site-bar a:hover {
  color: #FBD784;
  border-right: 3px solid #ffffffff;
}
